<template>
  <div>
    <!--安装插件-->
    <div style="margin-bottom: 10px">
      <el-button type="primary" size="small" @click="dialogFormVisible = true">安装插件</el-button>
    </div>
    <!--表格数据-->
    <el-table :data="plugins" border class="table"
              ref="multipleTable" header-cell-class-name="table-header">
      <el-table-column prop="pluginId" label="插件ID" width="300" sortable></el-table-column>
      <el-table-column prop="pluginDescription" label="描述" width="200"></el-table-column>
      <el-table-column prop="pluginClass" label="类路径" width="400"></el-table-column>
      <el-table-column prop="version" label="版本" width="100"></el-table-column>
      <el-table-column prop="requires" label="requires" width="80"></el-table-column>
      <el-table-column prop="provider" label="提供者" width="120"></el-table-column>
      <el-table-column label="操作" align="center">
        <template #default="scope">
          <el-link @click="unInstallPlugin(scope.row.pluginId)" type="primary" icon="el-icon-close">卸载</el-link>
        </template>
      </el-table-column>
    </el-table>
    <!--分页-->
    <div class="pagination">
      <el-pagination background layout="sizes, total, prev, pager, next" :current-page="pageNo"
                     :page-sizes="[5,10,20,50]" :page-size="pageSize" :total="pageTotal"
                     @current-change="handlePageChange"></el-pagination>
    </div>
    <!--插件安装弹框-->
    <el-dialog title="插件安装" :visible.sync="dialogFormVisible" width="25%">
      <el-form :model="form" :rules="rules" ref="form" label-position="left">
        <el-form-item label="组名" label-width="65px">
          <el-input v-model="form.groupName" autocomplete="off" style="width: 300px;" disabled></el-input>
        </el-form-item>
        <el-form-item label="组件ID" label-width="65px" prop="pluginId">
          <el-select v-model="form.pluginId" placeholder="请选择组件ID" style="width: 300px">
            <el-option v-for="item in pluginInstallList" v-bind:key="item.id" :label="item" :value="item"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="版本" label-width="65px" prop="version">
          <el-select v-model="form.version" placeholder="请选择组件版本" style="width: 300px">
            <el-option v-for="item in pluginInfoMap[form.pluginId]" v-bind:key="item.id" :label="item" :value="item"></el-option>
          </el-select>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="cancel">取 消</el-button>
        <el-button type="primary" @click="submit">确 定</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
export default {
  name: "Plugins",
  //父组件传值
  props: ['groupName'],
  data() {
    return {
      plugins: [],
      pageNo: 1,
      pageSize: 10,
      pageTotal: 0,
      dialogFormVisible: false,
      form: {
        groupName: this.groupName,
        pluginId: '',
        version: '',
        sync: false  //是否同步上传
      },
      //插件列表
      pluginInstallList: [],
      pluginInfoMap:{},
      //form校验
      rules: {
        pluginId: [
          { required: true, message: "插件ID不能为空", trigger: "blur" }
        ],
        version: [
          { required: true, message: "版本不能为空", trigger: "blur" }
        ],
      }
    }
  },
  mounted() {
    this.initData();
    this.initPlugins();
  },
  methods: {
    //清空
    clearData() {
      this.form = {
        groupName: this.groupName,
        pluginId: '',
        version: '',
        sync: false
      }
    },
    //初始化下拉框数据
    initData() {
      this.getRequest("mydataharbor/plugin/pageQueryForVue?pageNo=1&pageSize=10000").then(res=>{
        let records = res.records;
        this.pluginInfoMap = {};
        this.pluginInstallList = [];
        records.forEach(item=>{
          //插件列表
          this.pluginInstallList.push(item.pluginId);
          //版本
          this.pluginInfoMap[item.pluginId] = this.pluginInfoMap[item.pluginId] == null ? [] : this.pluginInfoMap[item.pluginId];
          this.pluginInfoMap[item.pluginId].push(item.version);
          item.children.forEach(child=>{
            this.pluginInfoMap[item.pluginId].push(child.version);
          });
        })
      })
    },
    //初始化插件列表
    initPlugins() {
      this.getRequest("mydataharbor/node/groupList").then(res=>{
        this.plugins = res.data[this.groupName].installedPlugins;
      })
    },
    //卸载插件
    unInstallPlugin(pluginId){
      console.log("uninstall" + pluginId);
      this.postRequest("/mydataharbor/node/uninstallPlugin?groupName=" + this.groupName + "&pluginId=" + pluginId).then(res=>{
        if (res.code == 0) {
          this.$message.info("插件卸载成功！");
          this.initPlugins();
        }
      })
    },
    //安装插件弹框按钮事件
    submit() {
      //安装插件请求
      this.$refs.form.validate(valid => {
        if (valid) {
          let url = "mydataharbor/node/installPlugin?groupName="+ this.form.groupName +
            "&pluginId="+ this.form.pluginId +"&version="+ this.form.version + "&sync=" + this.form.sync;
          this.postRequest(url).then(res=>{
            if (res.code == 0) {
              this.$message.info("插件安装成功！");
              this.initPlugins();
            } else {
              this.$message.error(res.msg);
            }
          });
          this.dialogFormVisible = false;
          this.clearData();
        } else {
          return false;
        }
      });
    },
    cancel() {
      this.dialogFormVisible = false;
      this.clearData();
    },
    //分页处理
    handlePageChange() {

    }
  }
}
</script>

<style scoped>
/deep/ .el-form-item__label{
  text-align: left;
}
/deep/ element.style {
  margin-left: 60px;
}
</style>



