<template>
  <div>
    <img :src="imgSrc"  alt="" />
   <p>MyDataHarbor是一个致力于解决任意数据源到任意数据端的分布式、高扩展性、高性能、事务级的中间件。</p>
   <span >
   <a href="https://bbs.mydataharbor.com">社区链接</a> &nbsp;
   <a href="https://github.com/xulang/mydataharbor">github链接</a>
   </span>
  </div>
</template>

<script>
export default {
  name: "Dashboard",
  data(){
    return {
      imgSrc:require('../../assets/logo.png')
    }
  }
}
</script>

<style scoped>
.background{
  width:100%;
  height:100%;  /**宽高100%是为了图片铺满屏幕 */
  z-index:-1;
  position: absolute;
}
</style>



