<template>
  <div>
    <el-container>
      <el-header class="homeHeader">
        <div class="title">MyDataHarbor</div>
      </el-header>
      <el-container style="height: 100%">
        <el-aside width="200px" style="margin-left: 5px;">
          <el-menu router unique-opened default-active="1" style="height: 100%">
            <el-menu-item index="../home">
              <i class="el-icon-menu" style="color: #409eff;margin-left: -5px"></i>
              <span slot="title">首页</span>
            </el-menu-item>

            <el-menu-item index="../cluster">
              <i class="el-icon-s-help" style="color: #409eff;margin-left: -5px"></i>
              <span slot="title">集群管理</span>
            </el-menu-item>
            <el-menu-item index="../plugin">
              <i class="el-icon-s-data" style="color: #409eff;margin-left: -5px"></i>
              <span slot="title">插件仓库</span>
            </el-menu-item>
            <el-menu-item index="../task">
              <i class="el-icon-s-cooperation" style="color: #409eff;margin-left: -5px"></i>
              <span slot="title">任务管理</span>
            </el-menu-item>
          </el-menu>
        </el-aside>
        <el-main>
          <!--面包屑 -->
          <el-breadcrumb separator-class="el-icon-arrow-right" v-if="this.$router.currentRoute.path!='/home'">
            <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
            <el-breadcrumb-item>{{this.$router.currentRoute.name}}</el-breadcrumb-item>
          </el-breadcrumb>
          <!--首页home -->
          <DashBoard v-if="this.$router.currentRoute.path=='/home' || this.$router.currentRoute.path=='/'"></DashBoard>
          <router-view class="homeRouterView"/>
        </el-main>
      </el-container>
    </el-container>
  </div>
</template>

<script>
import DashBoard from '../components/dashboard/Dashboard'
export default {
  name: "Home",
  components: {DashBoard},
  data() {
    return {

    }
  },
  computed: {

  },
  methods: {

  }
}
</script>

<style scoped>
.homeRouterView {
  margin-top: 10px;
}

.homeHeader {
  background-color: #409eff;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0px 15px;
  box-sizing: border-box;
}

.homeHeader .title {
  font-size: 30px;
  color: #f9ffff
}

.homeHeader {
  cursor: pointer;
}

.el-dropdown-link img {
  width: 38px;
  height: 38px;
  border-radius: 24px;
  margin-left: 8px;
}
</style>



