<template>
  <div>
    <el-tabs tab-position="left">
      <el-tab-pane :label="item.groupName" v-for="(item) in cluster" v-bind:key="item.id">
        <el-tabs active-name="first" style="margin-left: 10px">
          <!--节点管理-->
          <el-tab-pane label="节点" name="first">
            <Node :node-list="item.nodeInfos"> </Node>
          </el-tab-pane>
          <!--插件管理-->
          <el-tab-pane label="插件" name="second">
            <Plugins :group-name="item.groupName"></Plugins>
          </el-tab-pane>
          <!--任务管理-->
          <el-tab-pane label="任务" name="third">
            <Tasks :group-name="item.groupName"></Tasks>
          </el-tab-pane>
        </el-tabs>
      </el-tab-pane>
    </el-tabs>
  </div>
</template>

<script>
import Node  from "../../components/cluster/Node";
import Plugins from "../../components/cluster/Plugins";
import Tasks from "../../components/cluster/Tasks"

export default {
  name: "Cluster",
  components: {Tasks, Plugins, Node},
  data() {
    return {
      cluster:{},
    }
  },
  mounted() {
    this.initData();
  },
  methods: {
    initData() {
      this.getRequest("mydataharbor/node/groupList").then(res=>{
        //console.log(res);
        if (res.code == 0) {
          this.cluster = res.data;
        }
      })
    }
  }
}
</script>

<style scoped>
/deep/ .el-tabs--left .el-tabs__item.is-left {
  text-align: left;
}
</style>



